/* MenuPage.module.css */

.container {
    
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center; /* Vertically center */
    font-size: 30px;
    color: #fff;
    font-weight: 800;
    padding: 10%;
    min-height: 100vh;
    box-sizing: border-box;
    margin: 0;
  }

  
@media (max-width: 991px) {
    .container {
      padding-right: 20px;
      font-size: 40px;
    }
  }
  .header {
    position: relative;
    background: transparent;
    text-align: left;
    font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
  }
  .header h1 {
    background: transparent;
    font-size: 24px;
  }
  .header h2 {
    background: transparent;
    font-size: 44px;
  }
  .userName {
    display: inline-block; /* Make userName span inline-block */
    font-size: 44px;
    margin-left: 10px; /* Adjust spacing between "欢迎," and the name */
    font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
  }
  .buttonContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
  }
  .button {
    font-size: 30px;
    transition: color 0.3s, transform 0.3s;
    cursor: pointer;
    margin-top: 40px; /* Adjusted for better centering */
    padding-left: 30px;
    text-align: left;
    font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
  }
  
  .button:first-child {
    margin-top: 0;
  }
  .button:hover {
    color: rgb(0, 176, 230);
    transform: scale(1.3);
  }
  .button::before {
    content: '•';
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 30px;
    color: #fff;
  }
  .button::after {
    content: attr(data-description);
    position: absolute;
    transform: translateY(10px);
    top: 75%;
    left: 20%;
    white-space: nowrap;
    color: #fff;
    font-size: 14px; /* Make the text smaller */
    opacity: 0;
    transition: opacity 0.3s;
  }
  .button:hover::after {
    opacity: 1;
  }
  .footer {
    color: rgba(255, 255, 255, 0.38);
    margin-top: auto;
    font: 400 20px Roboto, sans-serif;
    align-self: flex-end;
  }
  @media (max-width: 991px) {
    .footer {
      margin: 40px 0 0 10px;
    }
  }
  .backgroundImageContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
  }
  
  .backgroundImage {
    height: auto;
    width: 100%;
    z-index: 0;
  }
  
  .backgroundImage1 {
    position: absolute;
    bottom: 0;
    width: 30%;
    height: 30%;
    right: 30%;
  }
  
  .backgroundImage2 {
    position: right;
    width: 50%;
    height: 100%;
  }