.body {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(10, 11, 22, 0.8);
}

.backgroundImage {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.content {
  text-align: center;
  z-index: 1; /* 콘텐츠를 앞으로 가져옵니다. */
  color: white;
  font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
}

.oops {
  font-size: 2rem;
  margin-bottom: 5px;
  font-family: 'Exo', sans-serif; /* 원하시는 글씨체를 지정하세요 */
  font-weight: 700;
}

.errorImage {
  width: 200px;
  height: auto;
  margin-bottom: 20px;
}

.message {
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
}

.button {
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-family: 'Yuanti TC', sans-serif; /* 원하시는 글씨체를 지정하세요 */
}

.button:hover {
  background-color: #0056b3;
}
