.div {
  background-color: rgba(0, 0, 0, 0.2); /* Dark translucent background */
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
 
}

.img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
  
}

.div3 {
  position: relative;
  background-color: rgba(4, 12, 31, 0.2); /* Darker translucent overlay */
  display: flex;
  width: 100%;
  padding-right: 54px;
  gap: 20px;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}

@media (max-width: 991px) {
  .div3 {
    max-width: 100%;
    flex-wrap: wrap;
    padding-right: 20px;
  }
}

.div4 {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  margin-bottom: 40%;
  
}

@media (max-width: 991px) {
  .div4 {
    max-width: 100%;
    margin-bottom: 50px;
  }
}

.div5 {
  display: flex;
  flex-direction: column;
  padding: 70px 80px;
  height: 100%;
  justify-content: flex-end;
}

@media (max-width: 991px) {
  .div5 {
    max-width: 100%;
    padding: 0 20px;
  }
}

.img4 {
  aspect-ratio: 1.85;
  object-fit: auto;
  object-position: center;
  width: 153px;
  max-width: 100%;
}

.img3 {
  aspect-ratio: 6.25;
  object-fit: auto;
  object-position: center;
  width: 557px;
  align-self: center;
  margin-top: 340px;
  margin-bottom: 30px;
  
}

@media (max-width: 991px) {
  .img3 {
    max-width: 100%;
    margin-top: 40px;
  }
}

.div7 {
  background: linear-gradient(180deg, #fff 0%, rgba(255, 255, 255, 0.44) 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font: 400 35px/68px Yuanti TC, -apple-system, Roboto, Helvetica, sans-serif;
  margin-bottom: 30px;
  margin-top: 0%;
}

@media (max-width: 991px) {
  .div7 {
    max-width: 100%;
    font-size: 40px;
    line-height: 66px;
  }
}

.div8 {
  align-self: center;
  display: flex;
  flex-direction: column;
}

@media (max-width: 991px) {
  .div8 {
    margin-top: 40px;
  }
}

.img2 {
  aspect-ratio: 3.03;
  object-fit: auto;
  object-position: center;
  width: 100%;
}

.img5 {
  aspect-ratio: 1.35;
  object-fit: auto;
  object-position: center;
  width: 193px;
  align-self: center;
  margin-top: 10px;
  max-width: 100%;
  cursor: pointer;
}

.hoverEffect:hover {
  opacity: 0.7;
}

.loginButtonContainer {
  position: absolute;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loginButton {
  width: 100%;
  padding: 12px;
  background-color: #498deb;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 20%;
  margin-right: 10px;
}

.newButton {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(45deg, #61dafb, #21a1f1); /* Gradient background */
  border: none;
  color: rgb(40, 8, 82);
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold; /* Bold the text */
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s, ;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 0;
}

.newButton:hover {
  background: linear-gradient(45deg, #21a1f1, #61dafb); /* Reverse the gradient on hover */
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2); 
}