body {
  margin: 0;
  font-family: Roboto, sans-serif;
  background-color: #040C1F;
  color: #fff;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

.recommendation-filter {
  text-align: center;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(7, 19, 49);
  background-image: linear-gradient(to bottom, rgb(22, 52, 129), #040C1F);
  padding: 10px 20px;
}

.logo {
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000; /* Ensure it's above other content */
}
.logo-img {
  max-width: 150px;
  height: auto;
}

.back-to-menu {
  display: flex;
  justify-content: flex-end; 
  align-items: center;
}

.menu-img {
  max-width: 30px;
  height: auto;
  cursor: pointer;
}

.main-content {
  padding: 0px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-content h1 {
  font-size: 2rem;
  margin-bottom: 0;
  letter-spacing: 2px;
  color: #d8d8d8;
  width: 100%; /* Full width */
  text-align: center; /* Center text */
}

.main-content p {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  color: #d8d8d8;
  width: 100%; /* Full width */
  text-align: center; /* Center text */
}

.buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  max-width: 100%; /* Full width */
  margin: 0 auto;
}

.square-button {
  width: calc(33.333% - 10px);
  height: 150px;
  margin: 5px;
  border: none;
  background-size: cover;
  background-color: #333; 
  color: #ffffff;
  text-align: center;
  line-height: 150px;
  font-size: 1.8rem;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,2);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 2); 
}

.square-button span {
  display: inline-block;
  width: 100%;
  height: 100%;
  transition: transform 0.3s;
}

.square-button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  transition: background 0.3s;
}

.square-button:hover::before {
  background: rgba(0, 0, 0, 0.3);
}

.square-button.selected::before {
  background: none;
}

.square-button:hover {
  transform: scale(1.1);
}

.square-button.selected::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0);
}

.submit-button {
  display: flex;
  justify-content: center;
  padding: 10px;
  width: 100%;
}

.rect-button {
  width: 150px;
  height: 40px;
  background-color: #007BFF;
  background-image: linear-gradient(135deg, #00c6ff 0%, #0072ff 100%);
  border: none;
  color: #fff;
  text-align: center;
  line-height: 40px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 25px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}

.rect-button:hover {
  background-image: linear-gradient(135deg, #0072ff 0%, #00c6ff 100%);
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .square-button {
    width: calc(20% - 10px);
  }
}

@media (min-width: 1024px) {
  .square-button {
    width: calc(14.2857% - 10px);
  }
}
