.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #050a24; /* 배경 색상 설정 */
}

.logo {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 120px;
  height: auto;
}

.backgroundImageContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
}

.backgroundImage {
  height: auto;
  width: 100%;
  z-index: 0;
}

.backgroundImage1 {
  position: absolute;
  bottom: 0;
  width: 30%;
  height: 30%;
  right: 30%;
}

.backgroundImage2 {
  position: right;
  width: 50%;
  height: 100%;
}

.form {
  width: 100%;
  max-width: 400px;
  padding: 100px;
  background-color: rgba(2, 6, 25, 0.479);
  border-radius: 10px;
  text-align: center;
  z-index: 0;
  
}

.div2 {
  margin-bottom: 20px;
}

.div3 {
  color: #d1d1da;
  font-size: 16px;
  font-weight: 400;
}

.div4 {
  color: #fff;
  font-size: 28px;
  font-weight: 600;
  margin-top: 10px;
}

.div5 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.emailLabel {
  color: #d9d9d9;
  font-size: 16px;
  margin-right: 10px;
}

.inputContainer {
  flex: 1;
}

.input {
  width: calc(100% - 20px); /* loginButton의 넓이 120px 고려 */
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 12px; /* loginButton의 여백과 동일하게 설정 */
}

.div10 {
  margin-bottom: 20px;
}

.loginButton {
  width: 100%;
  padding: 12px;
  background-color: #1570ef;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.loginButton:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.div12 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.div13 {
  color: #98a2b3;
  font-size: 14px;
  margin-right: 5px;
}

.div14 {
  color: #00b0e7;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
}
