.loading-container {
  position: fixed; /* Fixed positioning to cover the entire screen */
  top: 0; /* Top margin 0 */
  left: 0; /* Left margin 0 */
  width: 100%; /* Width 100%, covering full width */
  height: 100%; /* Height 100%, covering full height */
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  display: flex; /* Use flex layout */
  justify-content: center; /* Horizontally center */
  align-items: center; /* Vertically center */
  overflow: hidden; /* Hide overflow */
}

.background-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1; /* Place behind other elements */
}

.background-image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  filter: blur(4px) brightness(50%); /* Apply blur and dim */
}

.image-container {
  position: relative;
  z-index: 2; /* Ensure images appear above the background */
  flex: 1; /* Image container occupies most of the space */
  display: flex; /* Use flex layout */
  justify-content: center; /* Horizontally center */
  align-items: center; /* Vertically center */
  width: 100%; /* Width 100%, covering full width */
  height: 95%;
  margin-bottom: 1%;
}

.random-image {
  max-width: 100%; /* Maximum width of the image */
  max-height: 95%; /* Maximum height of the image */
  opacity: 0; /* Start with images hidden */
  object-fit: contain; /* Make sure the image fits within the container without overflow */
  transition: opacity 1s ease-in-out; /* Smooth transition for opacity */
  position: absolute; /* Position absolute to stack images */
}

.random-image.active {
  opacity: 1; /* Show the active image */
}

.loading-overlay {
  position: absolute;
  bottom: 10 px;
  right: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-center;
  z-index: 2;
}

.loading-gif {
  width: 50px; /* Set width of the loading gif */
  height: 50px; /* Set height of the loading gif */
}

.loading-text {
  position: relative;
  bottom: 10px;
  right: 10px;
  color: rgb(255, 255, 255);
  font-size: 18px;
  font: Roboto;
  margin-left: 15px;
  z-index: 3; /* Ensure the text is above other elements */
}

.loading-bar {
  width: 100%;
  height: 10px; /* Height of the loading bar */
  background-color: #fff; /* Background color of the loading bar */
  position: relative;
  overflow: hidden;
  margin-top: 10px;
  border-radius: 5px; /* Rounded edges */
}

.loading-bar-progress {
  height: 100%;
  background-color: rgb(22, 152, 169); /* Color of the progress */
  position: absolute;
  top: 0;
  left: 0;
  transition: width 0.1s linear; /* Smooth transition for width */
  border-radius: 5px; /* Rounded edges */
}
.loading-bar-container {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%; /* Adjust width as needed */
  z-index: 3; /* Ensure it is above other elements */
}


.loading-message {
  margin-top:0.5%;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
}